import type { DisplayMode, Endpoints } from "@/types.js";

export enum OAuthTokenTypes {
  ID_TOKEN = "id_token",
  ACCESS_TOKEN = "access_token",
  REFRESH_TOKEN = "refresh_token",
  ACCESS_TOKEN_EXPIRES_AT = "access_token_expires_at",
}

export const AUTH_SERVER_SESSION = "_session";
export const AUTH_SERVER_LEGACY_SESSION = "_session.legacy";

export enum CodeVerifier {
  COOKIE_NAME = "code_verifier",
  APP_URL = "app_url",
}
export enum UserStorage {
  USER = "user",
}
export interface CookieConfig {
  secure?: boolean;
  sameSite?: "strict" | "lax" | "none";
  domain?: string;
  path?: string;
  maxAge?: number;
  httpOnly?: boolean;
}
export type KeySetter = OAuthTokenTypes | CodeVerifier | UserStorage;

export type TokensCookieConfig = Record<
  OAuthTokenTypes | CodeVerifier,
  CookieConfig
>;

export type CivicAuthConfig = null | {
  clientId: string;
  redirectUrl: string;
  logoutRedirectUrl: string;
  oauthServer: string;
  endpoints: Endpoints;
  scopes: string[];
  nonce?: string;
  challengeUrl?: string;
  refreshUrl?: string;
  logoutUrl?: string;
  displayMode?: DisplayMode;
};
